<template>
    <div>
        <h1></h1>
        <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240">
            </el-table-column>

            <el-table-column prop="name" label="">
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            width="120">
                <template slot-scope="scope">
                <el-button type="text" size="small"
                @click="$router.push(`/departments/edit/${scope.row._id}`)"></el-button>
                <el-button type="text" size="small"
                @click="remove(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data(){
        return {
            items:[],
        }
    },
    methods:{
        async fetch(){
            const res = await this.$http.get('/rest/departments')
            this.items = res.data
        },
        async remove(row){
            this.$confirm(`${row.name}`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                const res = await this.$http.delete(`/rest/departments/${row._id}`)
                if(res.data.success){
                  this.$message({
                    type: 'success',
                    message: '!'
                  });
                }
                this.fetch()
                
            })
        }
    },
    created(){
        this.fetch()
    }
}
</script>
